import { Component} from '@angular/core';
import { IonicPage, NavParams, ViewController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'pick-type',
  templateUrl: 'pickType.html'
})
export class PickType {

  type: string;
  types: any;

  constructor(public viewCtrl: ViewController, params: NavParams) {
    this.types = [
      {
        name: '中国书画',
        selected: false,
        children: [
          {
            name: '山水',
            selected: false
          },
          {
            name: '花鸟',
            selected: false
          },
          {
            name: '人物',
            selected: false
          },
          {
            name: '画瓷',
            selected: false
          }
        ]
      },
      {
        name: '书法篆刻',
        selected: false,
        children: [{
          name: '楷书',
          selected: false
        }, {
            name: '行书',
            selected: false
          }, {
            name: '隶书',
            selected: false
          }, {
            name: '篆书',
            selected: false
          }, {
            name: '草书',
            selected: false
          }, {
            name: '篆刻',
            selected: false
          }, {
            name: '匾额',
            selected: false
          }, {
            name: '古籍文献',
            selected: false
          }]
      },
      {
        name: '西画雕塑',
        selected: false,
        children: [{
          name: '油画',
          selected: false
        }, {
            name: '雕塑',
            selected: false
          }, {
            name: '版画',
            selected: false
          }, {
            name: '水彩色粉',
            selected: false
          }, {
            name: '影像',
            selected: false
          }, {
            name: '其它',
            selected: false
          }]
      },
      {
        name: '古瓷杂项',
        selected: false,
        children: [{
          name: '青花',
          selected: false
        }, {
            name: '彩瓷',
            selected: false
          }, {
            name: '色釉瓷',
            selected: false
          }, {
            name: '外销瓷',
            selected: false
          }, {
            name: '文玩杂项',
            selected: false
          }, {
            name: '玉器',
            selected: false
          }, {
            name: '金属器',
            selected: false
          }]
      },
      {
        name: '当代工艺',
        selected: false,
        children: [{
          name: '竹木牙角',
          selected: false
        }, {
            name: '玉器',
            selected: false
          }, {
            name: '翡翠',
            selected: false
          }, {
            name: '珠宝',
            selected: false
          }, {
            name: '当代陶瓷',
            selected: false
          }, {
            name: '紫砂',
            selected: false
          }, {
            name: '名石',
            selected: false
          }, {
            name: '玛瑙',
            selected: false,
          }, {
            name: '琥珀蜜蜡',
            selected: false
          }, {
            name: '沉香檀香',
            selected: false
          }, {
            name: '金属器',
            selected: false
          }, {
            name: '海外艺术品',
            selected: false
          }, {
            name: '珊瑚',
            selected: false
          }, {
            name: '其它',
            selected: false
          }]
      }
    ]
  }

  ngAfterViewInit() {

  }

  setChildren(type) {
    if (type.selected) {
      type.children.forEach((item, index, list) => {
        item.selected = true;
      });
    } else {
      type.children.forEach((item, index, list) => {
        item.selected = false;
      });
    }
  }

  reset() {
    this.types.forEach((item, index, list) => {
      item.selected = false;
      item.children.forEach((item, index, list) => {
        item.selected = false;
      })
    });
  }

  confirm() {
    this.viewCtrl.dismiss({ types: this.types });
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }
}



